@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/pt-Mono-Regular.woff2") format("woff2"), url("../fonts/pt-Mono-Regular.woff2") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/roboto-Medium.woff2") format("woff2"), url("../fonts/roboto-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/robotoCondensed-Regular.woff2") format("woff2"), url("../fonts/robotoCondensed-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/robotoCondensed-Bold.woff2") format("woff2"), url("../fonts/robotoCondensed-Bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f2f5f7;
}

a {
  color: #041827;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f2f5f7;
  -webkit-text-fill-color: #041827;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "PT Mono", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.link-style {
  text-decoration: none;
}

.list-style {
  margin: 0;
  padding: 0;
  list-style: none;
}

.btn {
  display: block;
  padding: 10px 15px;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f7;
  background-color: inherit;
  border: 1px solid #f2f5f7;
  border-radius: 3px;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(4, 24, 39, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #f2f5f7;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.main-index {
  flex-grow: 1;
}

.main-footer {
  height: 50px;
}

.main-header {
  min-width: 320px;
  height: 120px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 1023px) {
  .main-header {
    height: 100px;
  }
}
@media (max-width: 767px) {
  .main-header {
    height: 70px;
  }
}

.main-header__wrapper {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  width: 89.7%;
  max-width: 1220px;
}
@media (max-width: 1023px) {
  .main-header__wrapper {
    min-width: 670px;
    width: 87%;
  }
}
@media (max-width: 767px) {
  .main-header__wrapper {
    min-width: 270px;
    width: 84.3%;
  }
}

.main-header__logo {
  margin-top: 30px;
}
@media (max-width: 1023px) {
  .main-header__logo {
    margin-top: 20px;
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .main-header__logo {
    margin-top: 15px;
  }
}

.main-header__action-buttons {
  margin-top: 45px;
}
@media (max-width: 1023px) {
  .main-header__action-buttons {
    margin-top: 20px;
  }
}
@media (max-width: 767px) {
  .main-header__action-buttons {
    margin-top: 10px;
  }
}

.main-header__set-call {
  width: 160px;
  height: 40px;
  margin: 35px 5.1% 0 0;
}
@media (max-width: 1023px) {
  .main-header__set-call {
    display: none;
  }
}

.logo-header__link {
  display: flex;
}
.logo-header__link img {
  margin-right: 15px;
}
@media (max-width: 767px) {
  .logo-header__link img {
    margin-right: 0;
    width: 35px;
    height: 35px;
  }
}

.logo-header__text {
  color: #f2f5f7;
}
@media (max-width: 767px) {
  .logo-header__text {
    display: none;
  }
}

.logo-header__title {
  margin: 5px 0;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 25px;
  font-weight: 700;
  line-height: 29px;
}

.logo-header__subtitle {
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  color: rgba(255, 255, 255, 0.7);
}

.action {
  display: flex;
  justify-content: space-between;
  width: clamp(365px, 37.8%, 461px);
}
@media (max-width: 1023px) {
  .action {
    width: max-content;
    margin-right: 3%;
  }
}

.action__link {
  display: flex;
  min-width: max-content;
  height: 20px;
  justify-content: center;
  line-height: 18px;
  color: #f2f5f7;
}
.action__link p {
  margin: 0;
}
@media (max-width: 1023px) {
  .action__link p {
    display: none;
  }
}
@media (max-width: 1023px) {
  .action__link {
    min-width: 40px;
    height: 40px;
  }
  .action__link:not(:last-child) {
    margin-right: 20px;
  }
}

.action__icon {
  display: block;
  width: 20px;
  height: 20px;
  margin-right: 15px;
}
.action__icon--mail {
  background-image: url("../img/svg/letter.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.action__icon--phone {
  background-image: url("../img/svg/phone-call.svg");
  background-position: center;
  background-repeat: no-repeat;
}
@media (max-width: 1023px) {
  .action__icon {
    margin: auto;
  }
}

.main-section {
  position: relative;
  top: -120px;
  z-index: -1;
  margin-bottom: -120px;
  padding-top: 305px;
  min-height: 880px;
  background-image: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../img/microchip-1x.jpg");
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: cover;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .main-section {
    background-image: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../img/microchip-2x.jpg");
  }
}
@media (max-width: 1023px) {
  .main-section {
    top: -100px;
    margin-bottom: -100px;
    padding-top: 180px;
    min-height: 640px;
    background-position: 20% 0;
  }
}
@media (max-width: 767px) {
  .main-section {
    top: -70px;
    margin-bottom: -70px;
    padding-top: 150px;
    min-height: 560px;
    background-position: 26% 0;
  }
}
.main-section h1 {
  margin: 0;
  margin-bottom: 40px;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 80px;
  font-weight: 700;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #f2f5f7;
}
@media (max-width: 1023px) {
  .main-section h1 {
    font-size: 64px;
    line-height: 75px;
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .main-section h1 {
    font-size: 36px;
    line-height: 42px;
  }
}
.main-section p {
  margin: 0;
  margin-bottom: 40px;
  font-size: 22px;
  font-weight: 400;
  line-height: 34px;
  color: #f2f5f7;
}
@media (max-width: 1023px) {
  .main-section p {
    margin-bottom: 50px;
  }
}
@media (max-width: 767px) {
  .main-section p {
    font-size: 14px;
    line-height: 22px;
  }
}

.main-section__button {
  width: 410px;
  height: 55px;
  padding: 15px 25px;
  font-size: 18px;
  line-height: 21px;
  background-color: #00b2ff;
  border: none;
}
@media (max-width: 1023px) {
  .main-section__button {
    margin-left: clamp(5px, 6.7%, 60px);
  }
}
@media (max-width: 767px) {
  .main-section__button {
    width: 270px;
    height: 40px;
    margin-left: 0;
    padding: 10px 30px;
    font-size: 14px;
    line-height: 16px;
  }
}

.main-section__wrapper {
  width: 89.7%;
  max-width: 1220px;
  margin: 0 auto;
  padding-left: clamp(50px, 10.2%, 120px);
}
@media (max-width: 1023px) {
  .main-section__wrapper {
    min-width: 670px;
    width: 87%;
    padding-left: 0;
  }
}
@media (max-width: 767px) {
  .main-section__wrapper {
    min-width: 270px;
    width: 84.3%;
  }
}

.benefits__wrapper {
  width: 89.7%;
  max-width: 1220px;
  margin: 120px auto;
}
@media (max-width: 1023px) {
  .benefits__wrapper {
    min-width: 670px;
    width: 87%;
    margin: 80px auto;
  }
}
@media (max-width: 767px) {
  .benefits__wrapper {
    min-width: 270px;
    width: 84.3%;
    margin: 50px auto;
  }
}

.privilege-list {
  display: grid;
  grid-template-columns: repeat(2, clamp(300px, 48.8%, 596px));
  grid-template-rows: repeat(2, 300px);
  gap: 29px 31px;
  width: 100%;
}
@media (max-width: 1023px) {
  .privilege-list {
    grid-template-columns: repeat(2, clamp(300px, 47.8%, 450px));
    grid-template-rows: repeat(2, 360px);
    gap: 30px 29px;
  }
}
@media (max-width: 767px) {
  .privilege-list {
    grid-template-columns: clamp(270px, 100%, 768px);
    grid-template-rows: repeat(4, auto);
    gap: 30px 0;
  }
}

.privilege-list__item {
  position: relative;
  padding: 40px 45px;
}
.privilege-list__item svg {
  margin-bottom: 20px;
  fill: #00b2ff;
}
.privilege-list__item h3 {
  margin: 0;
  margin-bottom: 15px;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 24px;
  line-height: 34px;
  color: #00b2ff;
}
@media (max-width: 767px) {
  .privilege-list__item h3 {
    font-size: 20px;
    line-height: 28px;
  }
}
.privilege-list__item p {
  margin: 0;
  font-family: "PT Mono", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #041827;
}
@media (max-width: 767px) {
  .privilege-list__item p {
    font-size: 14px;
    line-height: 19px;
  }
}
.privilege-list__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 5px;
  height: 100%;
  background-color: #00b2ff;
}
@media (hover: hover) {
  .privilege-list__item:hover:not(.focus-visible) {
    background-color: #00b2ff;
  }
  .privilege-list__item:hover:not(.focus-visible) svg {
    fill: #f2f5f7;
  }
  .privilege-list__item:hover:not(.focus-visible) h3 {
    color: #f2f5f7;
  }
  .privilege-list__item:hover:not(.focus-visible) p {
    color: #f2f5f7;
  }
}
@media (max-width: 1023px) {
  .privilege-list__item {
    padding: 40px 30px 35px 45px;
  }
}
@media (max-width: 1023px) {
  .privilege-list__item {
    padding: 30px;
    padding-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .privilege-list__item {
    min-height: 325px;
  }
}

